    &--header
    {
        padding: 1em !important;
    }
    &--home,&--tools,&--favoris
    {
        padding: 8rem 0rem 4rem 0rem !important; 
        @include lg
        {
            padding: 10rem 0 5rem 0 !important;
        }
    }
    &--favoris
    {
        min-height: 100vh;
    }
    &--footer
    {
        display: none;
        visibility: hidden;
    }
}


