.button
{
    &__favoris
    {
        padding-left: 1em;
        &::before
        {
            background-image: url(../assets/icones/heart-regular.svg) !important;
            z-index: 0;
        }
    }
    &__favoris-checked
        {
            &::before
            {
                background-image: url(../assets/icones/heart-solid.svg) !important;
                z-index: 1;
            }
        }
    &__favoris,&__favoris-checked
    {
        
        &::before
        {
            content: ""; // ::before and ::after both require content
            position: absolute;
            background-repeat: no-repeat;
            top: 50%;
            transform: translate(-50%,-50%);
            width: 50%;
            height: 50%;
            opacity: .7;
           
        }
    }
   /*  &__link,&__contribuer
    {
        
    } */
    
    &__contribuer
    {
        display: none !important;
        position: fixed !important;
        
        
        @include lg 
        {
            display: block !important;
            //right: -20px;
            right: 0;
            bottom: 25%;
            transform: rotate(90deg) translate(-50%, -100%);
            
        }
        @include xl
        {
           /*  display: block !important;
            right: 2vw;
            bottom: 20px;
            transform: inherit; */
        }
    }

}

.notification
{
    &__success
    {
        display: none;
        position: fixed !important;
        width: 100%;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        z-index: 1000;
    }
}


//scroll bar
/* width */
::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px $light; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: $grey-dark; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: $grey-dark; 
  }