$section-padding: 3rem 1.5rem !default;
$section-padding-medium: 9rem 1.5rem !default;
$section-padding-large: 18rem 1.5rem !default;

//primary
$white: var(--color-white);
$light: var(--color-light);
$grey: var(--color-grey);
$grey-dark: var(--color-grey-dark);
$dark: var(--color-dark);
$black: var(--color-black);


//secondary
$blue-light: var(--color-blue-light);
$blue: var(--color-blue);
$blue-dark: var(--color-blue-dark);
$blue-darken: var(--color-blue-darken);
$red: var(--color-red);
$yellow: var(--color-yellow); 


:root {
    //color 
    --color-white: #fff;
    --color-light: rgb(212, 212, 212);
    --color-grey: #A6A6A6; 
    --color-grey-dark:#595959;
    --color-dark: #262626; 
    --color-black: #0D0D0D; 

    --color-blue-light: #bbe1fa;
    --color-blue: #3282b8;
    --color-blue-dark: #0f4c75;
    --color-blue-darken: #1b262c;
    --color-red: #e21615;
    --color-yellow: #ffd257;
}


//RWD

$screen-xs: 0px;
$screen-sm: 768px;
$screen-md: 1023px;
$screen-lg: 1215px;
$screen-xl: 1407px;

