.card
{
    &__image
    {
        display: block;
        position: relative;
        padding-top: 75%;
        &--codes,&--inspiration,&--design,&--couleurs,&--typographie,&--compression,&--productivite,&--divers,&--tutoriels
        {
            background-size: contain;
            background-repeat: no-repeat;
            &:hover
            {
                animation: fade .5s;
            }
        }
        &--codes
        {
            background-image: url(../assets/images/categories/outils_codes.jpg);
            background-image: image-set(url(../assets/images/categories/outils_codes.jpg) 1x, url(../assets/images/categories/outils_codes@2x.jpg) 2x);
            &:hover
            {
                background-image: url(../assets/images/categories/outils_codes-hover.jpg);
                background-image: image-set(url(../assets/images/categories/outils_codes-hover.jpg) 1x, url(../assets/images/categories/outils_codes-hover@2x.jpg) 2x);    
            }
        }
        &--design
        {
            background-image: url(../assets/images/categories/outils_design.jpg);
            background-image: image-set(url(../assets/images/categories/outils_design.jpg) 1x, url(../assets/images/categories/outils_design@2x.jpg) 2x);
            &:hover
            {
                background-image: url(../assets/images/categories/outils_design-hover.jpg);
                background-image: image-set(url(../assets/images/categories/outils_design-hover.jpg) 1x, url(../assets/images/categories/outils_design-hover@2x.jpg) 2x);    
            }
        }
        &--inspiration
        {
            background-image: url(../assets/images/categories/outils_inspiration.jpg);
            background-image: image-set(url(../assets/images/categories/outils_inspiration.jpg) 1x, url(../assets/images/categories/outils_inspiration@2x.jpg) 2x);
