    }
    &__content
    {
        background-color: transparent;
        padding: 1.5rem;
    }
    @include lg 
    {
        //max-width: 60% !important;
    }
    &-suggestion
    {
        max-width: 80% !important;
        text-align: center !important;
        margin: 0 auto;
    }
}

.menu
{
    margin-left: 2em;
    position: absolute;
    max-width: 30vw;
    top: 50%;
    transform: translateY(-50%);
}
