
.home
{
    
    &__liste
    {

        //padding: $section-padding-medium !important;
        padding: 2.5rem 0 5rem 0 !important;
        max-width: 750px;
        margin: 0 auto;
        text-align: center;

        display: flex;
        flex-wrap: wrap;
        list-style: none;
        flex-basis: 100%;
        justify-content: space-between; //repartir les 20% qui reste entre les el        
        align-items: stretch;
        box-sizing:border-box;
        
        @include md
        {
            padding:3em auto ;
        }
        @include lg
        {
           
        }
        @include xl
        {
            max-width: 950px;
        }
        &-el
        {
            flex-basis: 40%;
            text-align: center;
            margin: 1em auto;
            //padding: 1.5em ;
            @include md
            {
                flex-basis: 30%;
                margin: 2em auto;
            }
            @include lg
            {
                margin: 1em auto;
                flex-basis: 30%;
            }
            @include xl
            {
                margin: 1em auto;
                //padding: 1em;
            }
            & a 
            {
                color: $dark;
            }
            
        transition: all .2s ease-in-out;
        } &-el:hover
        {
          transform: scale(1.05);
           
        }
    }
    
}


.codes,.design,.inspiration,.typographie,.compression,.couleurs,.productivite,.tutoriels,.divers,.favoris
{
    
    max-width: 800px;
        margin: 4em auto 0 auto;
 
        @include md
        {
            width: 75vw;
        }
        @include lg 
        {
            width: 60vw;
        }
        @include xl
        {
            width: 50vw;
        }

    &__el
    {
        margin-bottom: 4em;
        &-content
        {
            padding: 2rem 0;
            //padding: 1em 0 2em 0;
           /*  & a:last-child
            {
                margin-top: 1em !important;
                margin-right: 2em !important;
            } */
            @include lg 
            {
                padding: 2em 0;
            }
        }
        &--empty
        {
            display: none;
        }
    }

    
}
 
















