/* is-family-sans-serif	Sets font family to $family-sans-serif
is-family-monospace	Sets font family to $family-monospace
is-family-primary	Sets font family to $family-primary
is-family-secondary	Sets font family to $family-secondary
is-family-code	Sets font family to $family-code 

https://bulma.io/documentation/modifiers/typography-helpers/
*/


@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap');

html 
{
    font-size: 16px;
    @include md 
    {
        font-size: 19px;
    }
    @include lg
    {
        font-size: 16px;
    }
    @include xl
    {
        font-size: 16px;
    }
}
body
{
    
    /* la taille est gérer par le framework bulma  */
    font-family: 'Lora', serif !important;
    font-weight: 400 !important;
    line-height: 160% !important; 
  
}

/* 
rapport hiérarchique
is-size-1	3rem
is-size-2	2.5rem
is-size-3	2rem
is-size-4	1.5rem
is-size-5	1.25rem
is-size-6	1rem
is-size-7	0.75re */



.title
{
    font-family: 'Montserrat', sans-serif;
    line-height: 160%;
    text-transform: uppercase;
    font-weight: 600;
    padding: 0 1rem;

    &--section
    {
        display: block;
        margin: 0 auto;
        font-weight: bolder;
        text-align: center;
        font-size: 1.5em !important;
        @include md 
        {
            font-size: 2em !important;
        }
        @include lg
        {
            font-size: 2em !important;
            max-width: 50vw;
        }
    }
    &--modal
    {
        display: block;
        font-weight: bolder;
        text-align: center;
        @include lg 
        {
            font-size: 1.25rem !important;
            margin: 1.25rem auto 2.5rem auto;
        }
        @include xl
        {
            font-size: 1.5rem !important;
            margin: 1.5rem auto 3rem auto;
        }
    }
}

.breadcrumb
{
    font-family: 'Montserrat', sans-serif;
    line-height: 140%;
}
.subtitle
{
    
    line-height: 150% !important;
    font-weight: 600 !important;
}

.home 
{
    &__liste-el 
    {
        font-weight: 500;
        //text-transform: uppercase;
    }
}