
.navbar
{
    position: fixed !important;
    width: 100%;
    transition-duration: .5s;
    &-item
    {
        padding: 1vh ;
    }
    &-menu
    {
        @include sm 
        {
            height: 100vh !important;
            width: 100vw;
            overflow: hidden !important;
            z-index: 1000000 !important;
            position: fixed;
        } 
    }
    @include lg 
    {
        height: 3.75rem;
        padding: 0 10vw;
    }
    @include lg 
    {
        padding: 0 15vw;
    }
    &-touch
    {
        background: #f9f9f9;
     
        &:not(:first-child)
        {
            margin-top: 0.5em;
        }
        @include lg 
        {
            &:not(:first-child)
            {
                margin-top:inherit;
            }
            background: inherit;
        }
    }
}

.navbar--hidden
{
    transform: translateY(-100%);
}

